.list {
    color:white;
    background-color:crimson;
    width: 80%;
    height: 50px;
    margin: 20px auto;
    border-radius: 10px;
}

.list p {
    padding: 10px;
    position: relative;
    text-align: left;
}

.list p span {
    position: absolute;
    right:10px;
    cursor: pointer;
}

.faicons {
    margin: 0 10px;
    font-size: 16px;
    padding:6px 0;
}

.list p input {
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 16px;
    padding: 5px 0;
    font-weight: bold;
}

.list p input:focus {
    outline: none;
}