body {
  background-color: ghostwhite;
  width:100%;
}

.App {
  background-color: whitesmoke;
  min-height: 500px;
  width: 50%;
  margin: 40px auto;
  box-shadow: 0px 0px 30px lightgray;
  text-align:center;
}

#to-do-form input {
  background-color:whitesmoke;
  border: 1px grey solid;
  width: 50%;
  height:50px;
  padding: 0 20px;
  margin: 20px 10px 20px 0;
  font-size: 16px;
  border-radius: 10px;
  color:crimson;
}

#to-do-form input::placeholder {
  color: gray;
}

#to-do-form input:focus, #to-do-form button:focus {
  outline: none;
}

#to-do-form button {
  height: 50px;
  width: 20%;
  border-radius: 100px;
  border: 0;
  background:crimson;
  cursor: pointer;
  color: white;
  font-size: 14px;
}
